import cn from "classnames";
import { useState, useEffect } from "react";

import { Text } from "shared/components/Text";
import { Button } from "shared/components/Button";
import { LoadingStatus } from "shared/components/LoadingStatus";
import {
	apiCreateConversation,
	apiGetConversation,
	apiSendMessage,
} from "utils/api";

import css from "./styles.module.scss";

const map = {
	"user-host": "jan kochanowski",
	"user-client": "krzysztof jarzyna",
};

export const Conversation = ({
	username,
	conversationId,
	setUsername,
	setConversationId,
	mode,
	setMode,
}) => {
	const [conversation, setConversation] = useState(null);
	const [shouldRefetch, setShouldRefetch] = useState(false);
	const [isLoading, setIsLoading] = useState(false);
	const [message, setMessage] = useState("");

	const sendMessage = async (message) => {
		if (!message) {
			return;
		}
		setIsLoading(true);
		const response = await apiSendMessage(conversationId, message);
		setIsLoading(false);
		setShouldRefetch(true);
	};

	useEffect(() => {
		const createConversation = async () => {
			setIsLoading(true);
			const response = await apiCreateConversation(username);
			setIsLoading(false);
			if (response.id) {
				setConversationId(response.id);
			}
		};

		if (mode === "create") {
			createConversation();
			setMode("default");
		}
	}, [mode]);

	useEffect(() => {
		const getConversation = async () => {
			setIsLoading(true);
			const response = await apiGetConversation(conversationId);
			setIsLoading(false);
			setConversation(response);
		};

		if (shouldRefetch) {
			getConversation();
			setShouldRefetch(false);
		}
	}, [shouldRefetch]);

	useEffect(() => {
		const getConversation = async () => {
			setIsLoading(true);
			const response = await apiGetConversation(conversationId);
			setIsLoading(false);
			setConversation(response);
		};

		if (conversationId && !conversation) {
			getConversation();
		}
	}, [conversationId]);

	if (isLoading) {
		return <LoadingStatus />;
	}

	// console.log(conversation);

	return (
		<div className={css.Conversation}>
			<div className={css.content}>
				<button
					className={css.absoluteButton}
					onClick={() => {
						setConversationId(null);
						setUsername(null);
					}}
				>
					<Text className={css.absoluteButtonCaption}>X</Text>
				</button>
				<Text className={css.t1}>{map[username]}</Text>
				<div className={css.conversationContainer}>
					{conversation &&
						conversation.messages.map((el, i) => {
							if (el.text) {
								return (
									<div key={i} className={css.messageContainer}>
										<div
											className={cn(css.message, {
												[css.userMessage]: el.authorUsername !== username,
												[css.otherMessage]: el.authorUsername === username,
											})}
										>
											<Text className={css.t2}>{el.text}</Text>
										</div>
									</div>
								);
							}
						})}
				</div>
				<div className={css.inputContainer}>
					<input
						placeholder="Napisz wiadomo..."
						className={css.input}
						value={message}
						onChange={(e) => setMessage(e.current.value)}
					/>
					<Button
						text="Wylij"
						className={css.sendButton}
						onClick={() => sendMessage(message)}
					/>
				</div>
			</div>
		</div>
	);
};
